import React from 'react';
// import Defaultavatar from '../../assets/images/icon/avatar.svg';

const UserAvatar = ({ avatar, onClick, userId }) => {
  return (
    <>
      <div
        className='avatar'
        onClick={(e) => {
          onClick?.({ userId });
          e.stopPropagation();
          //   e.stopPropagation()
        }}
      >
        {avatar && (
          <img src={avatar} alt='userAvatar' className='user-avatar' />
        )}
      </div>
    </>
  );
};

export default UserAvatar;
